@keyframes loadblink {
    0% {
      opacity: 0; }
    25% {
      opacity: 0; }
    50% {
      opacity: 1; }
    75% {
      opacity: 0; }
    100% {
      opacity: 0; } }
  
  .loading__cursor {
    display: inline-block;
    width: .5rem;
    height: 1rem;
    background-color: var(--body);
    animation: loadblink 1s infinite; 
}